
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XHeader,
ViewBox,
Group,
XInput,
Cell,
Flexbox,
FlexboxItem,
XButton
},
data() {
return {
phone: '',
code: '',
timeCount: '',
retransmission: '',
gain: '获取验证码',
isGetCode: false,
showBtn: true,
success_code: ''
}
},
mounted() {
if(this.phone.length >= 1) {
this.showBtn = false;
} else {
this.showBtn = true;
}
},
watch: {
showBtn() {
if(this.phone.length >= 1) {
console.log("123")
} else {
console.log("456")
}
}
},
methods: {

obtain() {
var _this = this;
if(this.phone.length <= 0) {
this.$vux.toast.show({
text: "请输入手机号",
type: "text",
position: "middle"
});
return false;
}
if(!this.isPhone(this.phone)) {
this.$vux.toast.show({
text: "请输入正确手机号",
type: "text",
position: "middle"
});
return false;
};
this.gain = '';
this.timeCount = 3;
this.retransmission = 's后重发';
this.isGetCode = true;
var interval = setInterval(function(params) {
_this.timeCount--;
if(_this.timeCount <= 0) {
_this.timeCount = '';
_this.retransmission = '';
_this.isGetCode = false;
_this.gain = '获取验证码';
clearInterval(interval);
}
}, 1000)
this.$post(
"index/getCode", {
user_id: this.phone,
},
res => {
console.log(res);
console.log(res.msg);
this.success_code = res.msg;
if(res.res >= 1) {
var _this = this;
this.$vux.toast.show({
text: "发送成功",
type: "text",
position: "middle"
});
}
if(res.res < 0) {
this.$vux.toast.show({
text: "网络错误,请重试",
type: "text",
position: "middle"
});
return false;
}
}
);

},
isPhone(str) {
var reg = /^0?1[3|4|5|8][0-9]\d{8}$/;
return reg.test(str);
return true;
},
login() {
var phone = this.phone;
var code = this.code;
if(phone.length <= 0) {
this.$vux.toast.show({
text: "请输入手机号",
type: "text",
position: "middle"
});
return false;
}

if(!this.isPhone(phone)) {
this.$vux.toast.show({
text: "请输入正确手机号",
type: "text",
position: "middle"
});
return false;
};
if(code.length <= 0) {
this.$vux.toast.show({
text: "请输入验证码",
type: "text",
position: "middle"
});
return false;
};
this.$post(
"index/login", {
user_id: phone,
user_code: code
},
res => {
console.log(res);
if(res.res >= 1) {
var _this = this;
localStorage.user_id = phone;
localStorage.token = res.token;
this.$vux.toast.show({
text: "登陆成功",
type: "text",
position: "middle"
});
setTimeout(function() {
_this.$router.push({
replace: true,
path: '/index'
});
}, 1000);
this.$get("user/getUserInfo", {
field: ["user_name", "user_head"]
},
_res => {
console.log("------------", _res);
if(_res.res >= 1) {
localStorage.user_info = JSON.stringify(_res.msg);
return;
}
this.$vux.toast.show({
text: "用户信息获取失败，请重新登录",
type: "text",
position: "middle"
});
}
);
}
if(res.res < 0) {
this.$vux.toast.show({
text: "登录失败,请重试",
type: "text",
position: "middle",
width: '10em'
});
return false;
}
}
);
}
}
}



